import React from "react";

const BarGraphTooltip = props => {
  const { active, label, payload } = props;

  const getMessage = sortBy => {
    switch(sortBy) {
      case "damage":
        return "Damage per 10m";
      case "healing":
        return "Healing per 10m";
      default: 
        return "Damage per 10m";
    }
  };

  if (active && payload) {
    return (
      <div className="custom-tooltip">
        <p className="label">{`${payload[0].payload.name}`}</p>
        <p className="desc">{`Team: ${payload[0].payload.team}`}</p>
        <p className="desc">{`${getMessage(props.sortBy)}: ${Math.round(payload[0].value)}`}</p>
        <p className="desc">{`Rank: ${payload[0].payload.rank}`}</p>
      </div>
    );
  }
  return null;
}

export default BarGraphTooltip;
